@import "../common/styles/sass/variables";
@import "../common/styles/sass/mixins";

.mainBlock {
  height: 100vh;
  background-color: $bgColor;
  @media screen and (max-width: 565px) {
    height: fit-content;
    min-height: 100vh;
    padding:  80px 0 ;
  }
  .greetings {
    max-width: 600px;
    @media screen and (max-width: 1323px) {
      margin-top: 100px;
    }
    @media screen and (max-width: 565px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 20px;
    }

    .title {
      font-family: "Montserrat", sans-serif;
      color: #ffffff;
      font-weight: 600;
      font-size: 50px;
      line-height: 62px;
      margin: 22px 0;

      .name {
        color: $redColor;
        font-weight: 700;
      }

      .headerCaption {
        font-size: 35px;
        @media screen and (max-width: 767px) {
          font-size: 20px;
        }
      }
    }

  }

  .imageSlider {
    display: flex;
    flex-direction: row-reverse;
    @media screen and (max-width: 565px) {
      display: flex;
      width: 100%;
      justify-content: center;
    }
  }

  .btn {
      position: relative;
      width: 200px;
      height: 46px;
      border: 0 none;
      margin-top: 100px;
      transition: 0.4s;
      cursor: pointer;
      border-radius: 6px;
      font-family: $titleFontFamily;
      color: $textColor;
      background: linear-gradient(145deg, $redColor, #23272b);
      font-size: 14px;
      font-weight: 500;
      letter-spacing: 1px;
      text-transform: uppercase;
      &:hover {
        transform: translateY(-5px);
      }
    }


  .container{
    position: relative;
    @media screen and (max-width: 565px) {
      flex-direction: column-reverse;
    }
  }

  .imageContainer {
    position: relative;
    z-index: 1;
    width: 70%;
    &::before {
      position: absolute;
      z-index: -1;
      width: 100%;
      height: calc(100% - 16%);
      content: "";
      left: 50%;
      bottom: 0;
      -ms-transform: translateX(-50%);
      transform: translateX(-50%);
      background: #1e2024;
      box-shadow:  10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;;
      border-radius: 6px;
    }
    @media screen and (max-width: 767px) {
      height: 260px;
      width: 180px;
    }

    .photo {
      width: 100%;
      height: 100%;
      @media screen and (max-width: 565px) {
        margin-top: 20px;
      }
    }
  }

}
